import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class GameDataService {

  constructor() { }

  /* In-game data variables declaration */
  public playerStats: {gamesPlayed: number, levelsCleared: number, loses: number, score: number} | null = null;
  public isGamePaused: boolean = true;
  public currentLevelIndex: number = 0;

  /* Game session life counter declaration as Observable value */
  private life = new BehaviorSubject(2);
  public currentLife = this.life.asObservable();

  /* Game session score counter declaration as Observable value */
  private score = new BehaviorSubject(0);
  public currentScore = this.score.asObservable();

  /* Methods for handle with player stats */
  public setPlayerStats(value: {gamesPlayed: number, levelsCleared: number, loses: number, score: number} | null): void {
    this.playerStats = value;
  }

  public getCurrentPlayerStats():{gamesPlayed: number, levelsCleared: number, loses: number, score: number} | null {
    return this.playerStats
  } 

  public resetPlayerStats(): void {
    this.setPlayerStats(null);
  }

  public resetLevelIndex() : void {
    this.currentLevelIndex = 0;
  }
  
  /* Methods for handle with life data */
  public changeLife(life: number): void {
    this.life.next(life);
  }

  public getCurrentLife(): number {
    return this.life.getValue();
  }

  public resetLife(): void {
    this.changeLife(2);
  }

  /* Methods for handle with score data */
  public changeScore(score: number): void {
    this.score.next(score);
  }

  public getCurrentScore(): number {
    return this.score.getValue();
  }

  public resetScore(): void {
    this.changeScore(0);
  }

  public reset(): void {
    this.resetPlayerStats();
    this.resetLife();
    this.resetScore();
    this.resetLevelIndex();
  }

}
