import { PlayerListComponent } from './components/score/components/player-list/player-list.component';
import { AboutComponent } from './components/about/about.component';
import { SettingsComponent } from './components/settings/settings.component';
import { LevelDesignerComponent } from './components/level-designer/level-designer.component';
import { ScoreComponent } from './components/score/score.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { StartMenuComponent } from './components/start-menu/start-menu.component';
import { GameComponent } from './components/game/game.component';
import { ProfileComponent } from './components/score/components/profile/profile.component';
import { MyProfileComponent } from './components/score/components/my-profile/my-profile.component';

const routes: Routes = [
  {path: "", component: StartMenuComponent},
  {path: "game", component: GameComponent},
  {
    path: "score",
    component: ScoreComponent,
    children: [
      {
        path: "score-table",
        component: PlayerListComponent
      },
      {
        path: "my-profile",
        component: MyProfileComponent
      },
      {
        path: ":id",
        component: ProfileComponent
      }
    ]
  },
  {path: "designer", component: LevelDesignerComponent},
  {path: "settings", component: SettingsComponent},
  {path: "about", component: AboutComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
