import { RenderService } from './services/render/render.service';
import { Component, OnInit, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import firebase from "firebase/app";
import {firebaseConfig} from "./firebase.settings";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, AfterViewInit {
  @ViewChild("viewportContainer") viewportContainer!: ElementRef;
  
  constructor( private render: RenderService ){}

  ngOnInit(): void{
    firebase.initializeApp(firebaseConfig);
  }

  ngAfterViewInit(){
    const viewportSize = this.viewportContainer.nativeElement.getBoundingClientRect();
    this.render.CANVAS_SIZE.height = viewportSize.height;
    this.render.CANVAS_SIZE.width = viewportSize.width;
  }
}
