import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class PlayerListService {

  constructor() { }

  public isLoading: boolean = true;
  
  public playerBaseSnapshot: any = null;

  getPlayer(id: string): any {
    return this.playerBaseSnapshot.filter((item: any)=> item.nickname === id)[0]
  }
}
