import { DataService } from './../../../../services/data/data.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DatabaseService } from '../../../../services/database/database.service';
import { PlayerListService } from '../../../../services/player-list/player-list.service';


@Component({
  selector: 'app-authentication-form',
  templateUrl: './authentication-form.component.html',
  styleUrls: ['./authentication-form.component.scss']
})
export class AuthenticationFormComponent implements OnInit {

  constructor(public dataService: DataService, private databaseService: DatabaseService, private playerListService: PlayerListService) { }

  public signType: string = "none";
  public authError: boolean = false;

  public nickname: string = '';
  public email: string = '';
  public password: string = '';

  public registrate(model: any): void {
    const currentEssence = this;
    if (this.signType === "in") {
      this.databaseService.isAccessGrantedPromise(model.form.value).then(function (result: string | null) {
        if (typeof result === 'string') {
          currentEssence.dataService.signedPlayerId = result;
          currentEssence.dataService.setCookieId();
          currentEssence.dataService.toggleIsSigned();
        } else {
          currentEssence.authError = true;
        }
      })
    }
    if (this.signType === "up") {
      currentEssence.databaseService.registrationActivity(model.form.value);
      currentEssence.dataService.signedPlayerId = model.form.value.userNickname;
      currentEssence.databaseService.getUsersSnapshotPromise().then( function(snapshot: any) {
        currentEssence.playerListService.playerBaseSnapshot = Object.values(snapshot.val())
      }).then(function () {
        currentEssence.dataService.setCookieId();
        currentEssence.dataService.toggleIsSigned();
      })
    }

  }

  ngOnInit(): void {
  }

  signIn(): void {
    this.signType = "in"
  }

  signUp(): void {
    this.signType = "up"
  }

  getSignType(condition: string): boolean {
    return this.signType === condition
  }

  cancel(): void {
    this.email = "";
    this.nickname = "";
    this.password = "";
    this.signType = "none"
  }

}
