import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class SettingsService {

  constructor() { }

  SETTINGS = {
        control: {
            rocketSpeed: 10,
            isMouseControl: false,
            isAutoGame: false
        },
        audio: {
            volume: 0.5,
            soundFX: 0.5,
            music: 0.5
        },
        graphic: {
          isPlayOnFullScreen: false
        }
    }
  
  public toggleAutoGame(): void {
    this.SETTINGS.control.isAutoGame = !this.SETTINGS.control.isAutoGame;
  }
}
