import { RenderService } from './../../services/render/render.service';
import { DataService } from './../../services/data/data.service';
import { PlayerListService } from '../../services/player-list/player-list.service';
import { DatabaseService } from '../../services/database/database.service';
import { Component, OnInit } from '@angular/core';
import { Player } from './models/player.model';

@Component({
  selector: 'app-score',
  templateUrl: './score.component.html',
  styleUrls: ['./score.component.scss'],
  styles: [`.linkInactive{pointer-events : none;}`]
})
export class ScoreComponent implements OnInit {

  public playersBase: any;
  public viewportSize = {
    height: 0,
    width: 0
  }

  constructor(public dataService: DataService, private renderService: RenderService, public playerListService: PlayerListService , public dataBaseService: DatabaseService ) { }

  ngOnInit(): void {
    
    this.playersBase = this.setPlayersBase();
    this.viewportSize.height = this.renderService.CANVAS_SIZE.height;
    this.viewportSize.width = this.renderService.CANVAS_SIZE.width;
  }

  setPlayersBase() {
    const currentEssence = this;
    this.dataBaseService.getUsersSnapshotPromise().then( function(snapshot: any) {
      currentEssence.playerListService.playerBaseSnapshot = Object.values(snapshot.val())
    }).then(function () {
      currentEssence.playerListService.isLoading = false;
    })
  }

  public updateBase(): void {
    this.playerListService.isLoading = true;
    this.setPlayersBase();
  }

}
