import { Component, OnInit } from '@angular/core';
import { RenderService } from '../../../../services/render/render.service';

@Component({
  selector: 'app-brick',
  templateUrl: './brick.component.html',
  styleUrls: ['./brick.component.scss']
})
export class BrickComponent implements OnInit {

  constructor(private renderData: RenderService ) {
        this.BRICK_SPRITE.src = "../../../assets/sprites/brick.png";
    }

    private x = 0;
    private y = 0;
    private isAlive: boolean = false;

    BRICK_SPRITE = new Image(this.renderData.BRICK_SIZE.width, this.renderData.BRICK_SIZE.height);

    public setCoordinates(x: number, y: number) : BrickComponent{
        this.x = x;
        this.y = y;
        return this
    }

    public setIsAlive(value: boolean) : BrickComponent{
        this.isAlive = value;
        return this;
    }

    getIsAlive(){
        return this.isAlive;
    }

    getBoundaries(){
        return {
            top: this.y,
            right: this.x + this.renderData.BRICK_SIZE.width,
            bottom: this.y + this.renderData.BRICK_SIZE.height,
            left: this.x,
        }
    }

  ngOnInit(): void {
  }

}
