import { Injectable } from '@angular/core';
import firebase from "firebase/app";
import "firebase/database";
import { Player } from '../../components/score/models/player.model';

@Injectable({
  providedIn: 'platform'
})
export class DatabaseService {

  private databaseRootRef = firebase.database().ref();

  constructor() { }

  public getUsersSnapshotPromise() {
    return this.databaseRootRef.child('users').get();
  }

  private getUsersSecurityToken(value: string): any{
    return this.databaseRootRef.child('security').orderByChild('email').equalTo(value).once('value');
  }

  public isAccessGrantedPromise(userSecurity: {userEmail: string, userPassword: string}) : any {
    return this.getUsersSecurityToken(userSecurity.userEmail).then(function (snapshot : any) {
      const data = snapshot.val();
      return data
    }).then(function (data : any[] | null) {
      if (data === null) {
        return false
      } else {
        return Object.values(data)[0].id
      } 
    })
  }

  public registrationActivity(registrationData: any) {
    this.addNewPlayerSecurity(registrationData)
    this.addNewPlayer(Player.createPlayer(registrationData.userNickname), registrationData.userNickname)
  }


  addNewPlayerSecurity(player: any) {
    this.databaseRootRef.child('security').child(player.userNickname.split(".").join("_")).set({id: player.userNickname, email: player.userPassword, password: player.userPassword})
  }

  public addNewPlayer(player : Player, playerId: string){
    this.databaseRootRef.child('users').child(playerId).set(player);
  }

  public updatePlayerStats(playerId: string, stats: {gamesPlayed: number, levelsCleared: number, loses: number, score: number}) : any{
    const playerRef = this.databaseRootRef.child('users').child(playerId);
    const update = (new Date()).toISOString();
    const updates = {
      '/stats': stats,
      '/lastUpdate': update
    }
    return playerRef.update(updates);
  }

  
}
