import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';



@Injectable({
  providedIn: 'root'
})
export class DataService {

  public signedPlayerId : string = "";

  public isSigned: boolean = false;

  public isGameSaved: boolean = true;
  

  constructor() {
    this.getIdFromCookie()
  }

  toggleIsSigned(): void {
    this.isSigned = !this.isSigned;
  }

  getIdFromCookie():void {
    function getCookie(name: string):any {
      let matches = document.cookie.match(new RegExp(
        "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
      ));
      return matches ? decodeURIComponent(matches[1]) : undefined;
    }

    const idFromCookie = getCookie('myId');

    if (idFromCookie === undefined) {
      this.isSigned = false;
      this.signedPlayerId = "";
    } else {
      this.isSigned = true;
      this.signedPlayerId = idFromCookie;
    }

  }

  private setCookie(name: string, value: string, options: any) {

    const cookieOptions = {
      path: '/',
      //       
      ...options
    };
  
    if (options.expires instanceof Date) {
      options.expires = options.expires.toUTCString();
    }
  
    let updatedCookie = encodeURIComponent(name) + "=" + encodeURIComponent(value);
  
    for (let optionKey in cookieOptions) {
      updatedCookie += "; " + optionKey;
      let optionValue = cookieOptions[optionKey];
      if (optionValue !== true) {
        updatedCookie += "=" + optionValue;
      }
    }
  
    document.cookie = updatedCookie;
  }

  private deleteCookie(name: string) {
    this.setCookie(name, "", {
      'max-age': -1
    })
  }


  setCookieId():void {
    const expiredDate = new Date(Date.now()+(1000*60*60*24*7))

    this.setCookie('myId', this.signedPlayerId, {expires: expiredDate})
  }

  deleteCookieId(): void {
    this.deleteCookie('myId')
  }

  toggleIsGameSaved() : void {
    this.isGameSaved = !this.isGameSaved;
    console.log("game is saved :", this.isGameSaved)
  }

  // changeSignedPlayer(signedPlayerId: string) {
  //       this.signedPlayerId.next(signedPlayerId);
  //   }

  //   getCurrentScore() {
  //       return this.signedPlayerId.getValue();
  //   }

  //   resetScore() {
  //       this.changeSignedPlayer("");
  //   }
}
